import { cyclingSubSport, sport } from './consts';
export const routing = {
    '0.5': {
        // GCN
        goForward: () => '1.5',
    },
    '1': {
        // Sport
        goForward: (formValues) => {
            if (formValues.wasSubsportSelectionSkipped || formValues.sport !== sport.Cycling) {
                return '1.8';
            }
            return '1.5';
        },
    },
    '1.5': {
        // SubsportSelection
        goForward: (formValues, partner) => {
            if (formValues.wasPartnerSelectionSkipped) {
                if (partner) {
                    return '4';
                }
                return routing['2'].goForward(formValues);
            }
            return '1.8';
        },
        goBackward: (formValues) => {
            if (formValues.whitelabel) {
                return '0.5';
            }
            else {
                return '1';
            }
        },
    },
    '1.8': {
        // ChosenRace
        goBackward: (formValues) => {
            if (formValues.sport === 'Cycling') {
                return '1.5';
            }
            if (formValues.sport === 'Hiking') {
                return '1.8';
            }
            return '1';
        },
        goForward: (formValues, partner) => {
            if (formValues.chosen_race) {
                if (partner) {
                    return '4';
                }
                return '2';
            }
            if (formValues.sport === 'Cycling') {
                return formValues.sub_sport === cyclingSubSport['Time Trial'] ? '5.35' : '5.3';
            }
            switch (formValues.sport) {
                case 'Triathlon':
                    return '5.1';
                case 'Running':
                    return '5.2';
                case 'Swimrun':
                    return '5.5';
                case 'Hiking': // straight to conditions
                    return '6';
                default:
                    return '0';
            }
        },
    },
    '2': {
        // PartnerSelection
        goBackward: (formValues) => {
            if (formValues.wasSubsportSelectionSkipped || formValues.sport !== sport.Cycling) {
                return '1.8';
            }
            return '1.5';
        },
        goForward: (formValues) => (formValues.event_partner ? '3' : '4'),
    },
    '2.5': {
        // PartnerSportSelection
        goBackward: () => '1',
        goForward: (formValues) => (formValues.sport ? '3' : '1'),
    },
    '3': {
        // EventSelection
        goBackward: (formValues, partner) => {
            if (partner) {
                return '1.8';
            }
            if (formValues.event_partner === '29029') {
                return '3';
            }
            if (formValues.wasPartnerPreselected) {
                return '2.5';
            }
            if (formValues.wasPartnerSelectionSkipped) {
                return routing['2'].goBackward(formValues);
            }
            return '2';
        },
        goForward: (formValues) => {
            if (formValues.event_partner === '29029') {
                return '7';
            }
            return '5.4';
        },
    },
    '4': {
        // EventData
        goBackward: (formValues, partner) => {
            if (partner) {
                return routing['2'].goBackward(formValues);
            }
            if (formValues.wasPartnerSelectionSkipped) {
                if (formValues.chosen_race) {
                    return '1.8';
                }
                return routing['2'].goBackward(formValues);
            }
            return '2';
        },
        goForward: (formValues) => {
            if (formValues.sport === 'Cycling') {
                return formValues.sub_sport === cyclingSubSport['Time Trial'] ? '5.35' : '5.3';
            }
            switch (formValues.sport) {
                case 'Triathlon':
                    return '5.1';
                case 'Running':
                    return '5.2';
                case 'Swimrun':
                    return '5.5';
                default:
                    return '0';
            }
        },
    },
    '5.1': {
        // DistanceDataTriathlon
        goBackward: (formValues) => {
            if (!formValues.chosen_race) {
                return '1.8';
            }
            return '4';
        },
        goForward: () => '6',
    },
    '5.2': {
        // DistanceDataRunning
        goBackward: (formValues) => {
            if (!formValues.chosen_race) {
                return '1.8';
            }
            return '4';
        },
        goForward: () => '6',
    },
    '5.3': {
        // DistanceDataCycling
        goBackward: (formValues) => {
            if (!formValues.chosen_race) {
                return '1.8';
            }
            return '4';
        },
        goForward: () => '6',
    },
    '5.35': {
        // DistanceDataCyclingTimeTrial
        goBackward: (formValues) => {
            if (!formValues.chosen_race) {
                return '1.8';
            }
            return '4';
        },
        goForward: () => '6',
    },
    '5.4': {
        // EventDistanceSelection
        goBackward: (formValues) => {
            if (!formValues.chosen_race) {
                return '1.8';
            }
            return '3';
        },
        goForward: () => '7',
    },
    '5.5': {
        // DistanceDataSwimrun
        goBackward: (formValues) => {
            if (!formValues.chosen_race) {
                return '1.8';
            }
            return '3';
        },
        goForward: () => '6',
    },
    '5.6': {
        // DistanceDataHiking
        goBackward: (formValues) => {
            if (!formValues.chosen_race) {
                return '1.8';
            }
            return '3';
        },
        goForward: () => '6',
    },
    '6': {
        // LikelyConditions
        goBackward: (formValues) => {
            if (formValues.sport === 'Cycling') {
                return formValues.sub_sport === cyclingSubSport['Time Trial'] ? '5.35' : '5.3';
            }
            if (formValues.event_partner === '29029') {
                return '3';
            }
            switch (formValues.sport) {
                case 'Triathlon':
                    return '5.1';
                case 'Running':
                    return '5.2';
                case 'Swimrun':
                    return '5.5';
                default:
                    return '0';
            }
        },
        goForward: () => '7',
    },
    '7': {
        // Intensity
        goBackward: (formValues) => {
            if (formValues.event_partner === '29029') {
                return '3';
            }
            if (!formValues.chosen_race) {
                return '6';
            }
            if (formValues.event_partner) {
                return '5.4';
            }
            return '6';
        },
        goForward: (formValues) => {
            switch (formValues.sport) {
                case 'Triathlon':
                    return '8.1';
                case 'Running':
                    return '8.2';
                case 'Cycling':
                    return '8.3';
                case 'Swimrun':
                    return '8.4';
                case 'Hiking':
                    return '8.5';
                default:
                    return '0';
            }
        },
    },
    '8.1': {
        // FinishTimeTriathlon
        goBackward: () => '7',
        goForward: () => '9',
    },
    '8.2': {
        // FinishTimeRunning
        goBackward: () => '7',
        goForward: () => '9',
    },
    '8.3': {
        // FinishTimeCycling
        goBackward: () => '7',
        goForward: () => '9',
    },
    '8.4': {
        // FinishTimeSwimrun
        goBackward: () => '7',
        goForward: () => '9',
    },
    '8.5': {
        // FinishTimeHiking
        goBackward: () => '7',
        goForward: () => '9',
    },
    '9': {
        // Sweat
        goBackward: (formValues) => {
            switch (formValues.sport) {
                case 'Triathlon':
                    return '8.1';
                case 'Running':
                    return '8.2';
                case 'Cycling':
                    return '8.3';
                case 'Swimrun':
                    return '8.4';
                case 'Hiking':
                    return '8.5';
                default:
                    return '0';
            }
        },
        goForward: () => '10',
    },
    '10': {
        // SweatSalt
        goBackward: () => '9',
        goForward: () => '11',
    },
    '11': {
        // MuscleCramp
        goBackward: () => '10',
        goForward: () => '12',
    },
    '12': {
        // Physiology
        goBackward: () => '11',
        goForward: () => '13',
    },
    '13': {
        // SubmitPage
        goBackward: () => '12',
    },
};
